.App {
  height: 100vh;
  background-color: #1a202c;
  overflow: scroll;
  text-align: center;
}
a{
  text-decoration:none;
  
}

.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 30px 0 30px;
}

.authed-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 30px;
}

.header {
  margin: 0;
  font-size: 50px;
  font-weight: bold;
  color: white;
}

.sub-text {
  font-size: 25px;
  color: white;
}

.gradient-text {
  background: -webkit-linear-gradient(left, #60c657 30%, #35aee2 60%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cta-button {
  height: 45px;
  border: 0;
  width: auto;
  padding-left: 40px;
  padding-right: 40px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  color: white;
}

.connect-wallet-button {
  background: -webkit-linear-gradient(left, #60c657, #35aee2);
  background-size: 200% 200%;
  animation: gradient-animation 4s ease infinite;
}

.submit-product-button {
  background: -webkit-linear-gradient(left, #4e44ce, #35aee2);
  background-size: 200% 200%;
  animation: gradient-animation 4s ease infinite;
  margin-left: 10px;
}

.footer-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  bottom: 0;
  left: 0;
  padding-bottom: 45px;
}

.twitter-logo {
  width: 35px;
  height: 35px;
}

.footer-text {
  color: white;
  font-size: 16px;
  font-weight: bold;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-gap: 1.5rem;
  justify-items: center;
  margin: 0;
  padding: 0;
}

.product-grid .product-item {
  display: flex;
  flex-direction: column;
  position: relative;
  justify-self: center;
  align-self: center;
  position:relative;
}

.product-item img {
  width: 100%;
  height: 300px;
  border-radius: 10px;
  object-fit: cover;
}

.product-item h3{
  color:#FFFFFF;
  font-size:1.3em;
  font-style:bold;
  margin:20px 0;
}

.product-item h4{
  color:#FFF;
  font-size:1em;
  font-style:italic;
  margin:0;
}
.product-item h5{
  color:#FFF;
  font-size:1em;
  font-style:italic;
  margin:20px 0 0;
}
.product-item a{
  color:#2128fd;
  font-size:.7em;
  font-style:italic;
}

.connected-container input[type='text'] {
  display: inline-block;
  color: white;
  padding: 10px;
  width: 20%;
  height: 60px;
  font-size: 16px;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.25);
  border: none;
  margin: 50px auto;
  border:1px solid #e4a23d;
}
.connected-container input[type='text'].name {
  border-radius: 10px 0px 0px 10px;
}

.connected-container button {
  height: 60px;
  background: #e4a23d;
  border-radius: 0 10px 10px 0;
}
.delete-btn{
  display:inline-block;
  width:48px;
  height:48px;
  border-radius:50%;
  font-style:normal !important;
  background:red;
  position:absolute;
  font-size:.9em !important;
  box-sizing:border-box;
  top:-5%;
  right:-5%;
  padding:10px;
}
.delete-btn img{
  width:100%;
  height:100%;
  display:block;
}